	module.exports = {"title":"Configuration","body":"<p class=\"edit-page\"><a href=\"https://github.com/alex285/myGNOME/blob/master/pages/docs/configuration/index.md\">Edit on Github</a></p>\n<div class=\"clear\"></div>\n<p>This page lists popular configuration options.</p>\n<h2>APP MENUS ON WINDOW</h2>\n<p>That will move the application menu from GNOME Shell top bar, to application window itself, and it will place a new icon on the left side of title or CSD bar.</p>\n<p>From GNOME Tweak Tool</p>\n<pre><code><span class=\"hljs-keyword\">Top</span> Bar -&gt;  <span class=\"hljs-keyword\">Show</span> Application <span class=\"hljs-keyword\">Menu</span> &gt; <span class=\"hljs-keyword\">Set</span> to <span class=\"hljs-keyword\">Off</span>\n</code></pre>\n<p>That does 2 things. First removes the App Menu from GNOME Shell</p>\n<pre><code>org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.settings-daemon</span><span class=\"hljs-selector-class\">.plugins</span><span class=\"hljs-selector-class\">.xsettings</span> overrides {<span class=\"hljs-string\">'Gtk/ShellShowsAppMenu'</span>: &lt;<span class=\"hljs-number\">0</span>&gt;}\n</code></pre>\n<p>Second it adds the menu on GTK apps ( Revisit this! )</p>\n<pre><code>org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.desktop</span><span class=\"hljs-selector-class\">.wm</span><span class=\"hljs-selector-class\">.preferences</span> button-layout <span class=\"hljs-string\">'appmenu:minimize,maximize,close'</span>\n</code></pre>\n<h2>ENABLE ALL WINDOW CONTROLS</h2>\n<p>By default GNOME will only use close button {x}. To enable minimize and maximize, from GNOME Tweak Tool</p>\n<pre><code>W<span class=\"hljs-function\"><span class=\"hljs-title\">indows</span> -&gt;</span> T<span class=\"hljs-function\"><span class=\"hljs-title\">itlebar</span> Buttons -&gt;</span> Set {Maximize, Minimize} to On\n</code></pre>\n<p>IF YOU WANT TO CHANGE THE ORDER</p>\n<p>You need to use DCONF-EDITOR. Usually systems don’t ship it by default. If you don’t have it</p>\n<pre><code>$ sudo apt <span class=\"hljs-keyword\">install</span> dconf-editor\n</code></pre>\n<p>Navigate to</p>\n<pre><code>org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.settings-daemon</span><span class=\"hljs-selector-class\">.plugins</span><span class=\"hljs-selector-class\">.xsettings</span> overrides\n</code></pre>\n<p>And you should see something like</p>\n<pre><code><span class=\"hljs-string\">'{'</span>Gtk/ShellShowsAppMen<span class=\"hljs-string\">u': &lt;0&gt;}'</span>\n</code></pre>\n<p>Now lets expand this value a bit by adding all the window controls</p>\n<pre><code>{<span class=\"hljs-symbol\">'Gtk</span>/<span class=\"hljs-type\">ShellShowsAppMenu'</span>: &lt;<span class=\"hljs-number\">0</span>&gt;, <span class=\"hljs-symbol\">'Gtk</span>/<span class=\"hljs-type\">DecorationLayout'</span>: &lt;<span class=\"hljs-symbol\">'menu</span>, :minimize,maximize,close'&gt;}\n</code></pre>\n<p class=\"box-danger\">The value is space sensitive!</p>\n<p>That won’t change anything, and we don’t want to change anything yet! Now try re-arranging the controls adding close first</p>\n<pre><code>{<span class=\"hljs-symbol\">'Gtk</span>/<span class=\"hljs-type\">ShellShowsAppMenu'</span>: &lt;<span class=\"hljs-number\">0</span>&gt;, <span class=\"hljs-symbol\">'Gtk</span>/<span class=\"hljs-type\">DecorationLayout'</span>: &lt;<span class=\"hljs-symbol\">'menu</span>, :close,minimize,maximize'&gt;}\n</code></pre>\n<p>Now you see the controls arrangement will be {x _ [ ])</p>\n<p>If you want to move the close on the left side, you change</p>\n<pre><code>{<span class=\"hljs-symbol\">'Gtk</span>/<span class=\"hljs-type\">ShellShowsAppMenu'</span>: &lt;<span class=\"hljs-number\">0</span>&gt;, <span class=\"hljs-symbol\">'Gtk</span>/<span class=\"hljs-type\">DecorationLayout'</span>: &lt;<span class=\"hljs-symbol\">'close</span>,menu:minimize,maximize'&gt;}\n</code></pre>\n<p>That will work <strong>only for GTK Windows.</strong> If you want to apply the changes to everything, and for example to move all the controls to the left like Ubuntu, set</p>\n<pre><code>org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.desktop</span><span class=\"hljs-selector-class\">.wm</span><span class=\"hljs-selector-class\">.preferences</span> button-layout <span class=\"hljs-string\">'close,maximize,minimize,appmenu:'</span>\n</code></pre>\n<p>and</p>\n<pre><code>org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.settings-daemon</span><span class=\"hljs-selector-class\">.plugins</span><span class=\"hljs-selector-class\">.xsettings</span> overrides {<span class=\"hljs-string\">'Gtk/ShellShowsAppMenu'</span>: &lt;<span class=\"hljs-number\">0</span>&gt;}\n</code></pre>\n<p>From CLI you can simply give these two</p>\n<pre><code>$ gsettings set org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.desktop</span><span class=\"hljs-selector-class\">.wm</span><span class=\"hljs-selector-class\">.preferences</span> button-layout <span class=\"hljs-string\">'close,maximize,minimize,appmenu:'</span>\n$ gsettings set org<span class=\"hljs-selector-class\">.gnome</span><span class=\"hljs-selector-class\">.settings-daemon</span><span class=\"hljs-selector-class\">.plugins</span><span class=\"hljs-selector-class\">.xsettings</span> overrides <span class=\"hljs-string\">\"{'Gtk/ShellShowsAppMenu': &lt;0&gt;}\"</span>\n\n</code></pre>\n<p class=\"box-warning\">This guide needs a rewrite, but you get the point!</p>\n<h2>SET GLOBAL GTK THEME TO DARK</h2>\n<p>From GNOME Tweak Tool</p>\n<pre><code>Appearance -&gt; <span class=\"hljs-built_in\">Global</span> Dark Theme -&gt; <span class=\"hljs-built_in\">Set</span> <span class=\"hljs-keyword\">to</span> <span class=\"hljs-keyword\">On</span>\n</code></pre>\n<p>That actually adding</p>\n<pre><code><span class=\"hljs-variable\">$ </span>less ~<span class=\"hljs-regexp\">/.config/gtk</span>-<span class=\"hljs-number\">3.0</span>\n\n[Settings]\ngtk-application-prefer-dark-theme=<span class=\"hljs-number\">1</span>\n</code></pre>\n<p>If you want the root user have dark theme  too, you can edit root’s GTK config</p>\n<p class=\"box-warning\">Notice the changes to be applied need restart. Some apps like Nautilus don’t close by {x}. You need to kill it and start it again</p>\n"}

