	module.exports = {"title":"Extensions","body":"<p class=\"edit-page\"><a href=\"https://github.com/alex285/myGNOME/blob/master/pages/docs/extensions/index.md\">Edit on Github</a></p>\n<div class=\"clear\"></div>\n<p>Extensions provide to GNOME Shell additional features, or modify interface elements. When you enable an extension, you actually hot-loading code\ndirectly to GNOME Shell. Extensions can cause weird issues, and when that is the case you should unload the extension, and try again.</p>\n<p class=\"box-warning\">When you disable an extension from GNOME Tweak Tool the extension is trying to revert the default Shell code, but it is not unloaded.\nYou should always restart GNOME Shell <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code><span class=\"hljs-symbol\">alt</span> + <span class=\"hljs-built_in\">f2</span> / r\n</code></pre>\n<p>In Wayland session, GNOME Shell cannot be restarted, so you need to relog.</p>\n<p class=\"box-info\">Because there isn’t an API for extensions, each extension can override another one, fully or partially,\ndepending the loading order.</p>\n<h2>HOW DO I INSTALL AN EXTENSION?</h2>\n<p>First you need GNOME Tweak Tool <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code>$ sudo apt <span class=\"hljs-keyword\">install</span> gnome-tweak-tool\n</code></pre>\n<p>You will find GNOME Shell extensions <a href=\"https://extensions.gnome.org\">on EGO</a> or in your Linux repositories. Starting from GNOME 3.22\nextensions will be available via GNOME Software App.</p>\n<p>Keep on mind that usually Github repos are far more updated than  EGO or distro repos, therefore is good idea to directly get some extensions from there.</p>\n<h2>WHERE THE EXTENSIONS ARE INSTALLED?</h2>\n<p><strong>On XDG-Folders.</strong> That will be  <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code>/usr/share/gnome-<span class=\"hljs-built_in\">shell</span>/extensions      (<span class=\"hljs-keyword\">system</span>-wide)\n~/.<span class=\"hljs-keyword\">local</span>/share/gnome-<span class=\"hljs-built_in\">shell</span>/extensions  (user-specific)\n</code></pre>\n<h2>NOTICEABLE EXTENSIONS</h2>\n<p class=\"my-subtext\"><a href=\"https://github.com/GNOME/gnome-shell-extensions\">The official GNOME Shell Extensions</a>\nThese are the extensions made by GNOME Project. 13 in total!</p>\n<p class=\"my-subtext\"><a href=\"https://github.com/micheleg/dash-to-dock\">Dash to Dock</a>\nA dock for the Gnome Shell. This extension moves the dash out of the overview transforming it in a dock for an easier launching\nof applications and a faster switching between windows and desktops.</p>\n<p class=\"my-subtext\"><a href=\"https://github.com/rockon999/dynamic-panel-transparency/\">Dynamic Transparency</a>\nAdds Transparency To The Gnome Shell Panel, with a fade in / out effects for max / unmax windows</p>\n<p class=\"my-subtext\"><a href=\"https://github.com/passingthru67/workspaces-to-dock\">Workspaces to Dock</a>\nA gnome shell extension that transforms the workspaces into an intellihide dock</p>\n<p class=\"my-subtext\"><a href=\"https://github.com/sustmi/gnome-shell-extensions-sustmi\">Window Overlay Icons</a>\nAdd application icons to GNOME Shell window overview</p>\n<h2>TIPS</h2>\n<p><strong>Disable Version Validation</strong></p>\n<p>If you’re using a new or unstable version of GNOME, you may notice that some extensions don’t get loaded. Thats happening because\nextensions check for compatibility against GNOME version. You can disable this check by\n<i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code>$ gsettings <span class=\"hljs-keyword\">set</span> org.gnome.shell <span class=\"hljs-keyword\">disable</span>-extension-<span class=\"hljs-keyword\">version</span>-<span class=\"hljs-keyword\">validation</span> <span class=\"hljs-literal\">true</span>\n</code></pre>\n"}

