	module.exports = {"title":"GNOME on Ubuntu","body":"<p class=\"edit-page\"><a href=\"https://github.com/alex285/myGNOME/blob/master/pages/docs/ubuntu/index.md\">Edit on Github</a></p>\n<div class=\"clear\"></div>\n<p>Ubuntu is, by far, the most popular Linux system today and that gives it the best community support you can get. Thus\nit is an obvious good choice for getting GNOME, and is a good distro in general!</p>\n<p>Although Ubuntu uses some GNOME components by default, Canonical patches these components so that they would work better under Unity (their desktop shell). This could cause some issues if we tried to use GNOME shell on top of these components.</p>\n<p class=\"box-info\">This is a step by step guide, and copy/pasting commands will work, but please check out the official documentation at the links provided!</p>\n<p><strong>So here is what you can do!</strong></p>\n<h2>UBUNTU GNOME</h2>\n<p>If you are willing to reinstall Ubuntu, it is a good idea to directly get <a href=\"https://ubuntugnome.org/\">Ubuntu GNOME</a> that comes with GNOME by default.</p>\n<p>Ubuntu GNOME is not officially connected with the GNOME Project, but it is an official spin of Ubuntu and has the official Ubuntu repos.</p>\n<h2>GNOME 3.18</h2>\n<p>At the current time, the latest Ubuntu release is 16.04 (April 2016), and GNOME’s latest release is 3.20 (March 2016). Ubuntu 16.04’s repos have GNOME 3.18 apart from GNOME Files (aka Nautilus) which is on version 3.14 and GNOME Software which is on version 3.20.</p>\n<h3>UPDATE GNOME FILES TO 3.18</h3>\n<p>You have to add the <a href=\"https://launchpad.net/~gnome3-team/+archive/ubuntu/gnome3\">GNOME 3 Team PPA</a> <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code>$ sudo<span class=\"hljs-built_in\"> add-apt-repository </span>ppa:gnome3-team/gnome3\n</code></pre>\n<p>Then update <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code><span class=\"hljs-variable\">$ </span>sudo apt update\n</code></pre>\n<p>And finally upgrade. That will get you GNOME Files 3.18 <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code><span class=\"hljs-variable\">$ </span>sudo apt upgrade\n</code></pre>\n<p>CONGRATZ! YOU’RE DONE!</p>\n<h2>GNOME 3.20</h2>\n<p>If you want to get GNOME 3.20 you need to add the <a href=\"https://launchpad.net/~gnome3-team/+archive/ubuntu/gnome3-staging\">GNOME 3 Team Staging PPA</a></p>\n<p class=\"box-warning\">That is a massive update and you might run into issues. Or you might not! In any case you should be familiar with downgrading packages using <a href=\"http://manpages.ubuntu.com/manpages/trusty/man1/ppa-purge.1.html\"><code>ppa-purge</code></a> </p>\n<p>To add the staging PPA <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code>$ sudo<span class=\"hljs-built_in\"> add-apt-repository </span>ppa:gnome3-team/gnome3-staging\n</code></pre>\n<p>Then update <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code><span class=\"hljs-variable\">$ </span>sudo apt update\n</code></pre>\n<p>And finally upgrade. You will get the latest and greatest GNOME! <i class=\"fa fa-arrow-circle-down\" aria-hidden=\"true\"></i></p>\n<pre><code><span class=\"hljs-variable\">$ </span>sudo apt upgrade\n</code></pre>\n<p>CONGRATZ! YOU’RE DONE! <br />\nNow you can continue browsing on GNOME 3.20 guides on this site! But there aren’t any yet! So come back later!</p>\n<h2>TIPS</h2>\n<p>Are any special configurations needed under Ubuntu GNOME?</p>\n"}

